import { useEffect } from 'react';
import { refreshChannelList } from '../utils';

function useHandleReconnect({ isOnline }, { sdk, allChannels, channelListDispatcher }) {
  useEffect(() => {
    if (!isOnline) {
      return () => {};
    }

    const timer = setTimeout(() => {
      refreshChannelList({ sdk, allChannels, channelListDispatcher });
    }, 2000);
    return () => clearTimeout(timer);
  }, [isOnline]);
}

export default useHandleReconnect;
