import React, {
  useState, useMemo, useEffect, useContext, useRef,
} from 'react';
// eslint-disable-next-line no-unused-vars
import PropTypes from 'prop-types';

const areSetsEqual = (a, b) => a.size === b.size && [...a].every((it) => b.has(it));

const SubscriptionContext = React.createContext({
  subscribe: () => {},
  unsubscribe: () => {},
});

const StorageContext = React.createContext({});

export default function ExternalUserProfileProvider(
  // eslint-disable-next-line react/prop-types
  { onProfileListRequested, profileDataMap, children },
) {
  const [subscriptions, setSubscriptions] = useState([]);
  const requestListRef = useRef(new Set());

  const subscriptionApi = useMemo(() => ({
    subscribe: (newProfileList) => {
      setSubscriptions(
        (prevSubscriptions) => prevSubscriptions
          .filter((it) => it !== newProfileList)
          .concat([newProfileList]),
      );
    },
    unsubscribe: (existingProfileList) => {
      setSubscriptions(
        (prevSubscriptions) => prevSubscriptions
          .filter((it) => it !== existingProfileList),
      );
    },
  }), []);

  useEffect(() => {
    const flatList = subscriptions.reduce((acc, val) => acc.concat(val), []);
    const newRequestedList = new Set(flatList);

    if (!areSetsEqual(newRequestedList, requestListRef.current)) {
      requestListRef.current = newRequestedList;
      onProfileListRequested(Array.from(newRequestedList));
    }
  }, [subscriptions, onProfileListRequested]);

  return (
    <SubscriptionContext.Provider value={subscriptionApi}>
      <StorageContext.Provider value={profileDataMap}>
        {children}
      </StorageContext.Provider>
    </SubscriptionContext.Provider>
  );
}

ExternalUserProfileProvider.SubscriptionContext = SubscriptionContext;
ExternalUserProfileProvider.StorageContext = StorageContext;

export function useExternalUserProfileSubscription(profileList) {
  const subscriptionApi = useContext(SubscriptionContext);

  return useEffect(() => {
    subscriptionApi.subscribe(profileList);
    return () => subscriptionApi.unsubscribe(profileList);
  }, [subscriptionApi, profileList]);
}

export function useExternalUserProfiles(userIds) {
  const storage = useContext(StorageContext);

  if (!userIds) {
    return {};
  }

  if (Array.isArray(userIds)) {
    return userIds.map((id) => storage[id] || {});
  }

  return storage[userIds];
}
